
:root {
  font-size: 18px;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  --transition-speed: 600ms;
}

.dark {
  --text-primary: #F0E6EF;
  --text-secondary: #FFFFFF;
  --bg-primary: #2C2B3C;
  --bg-clear: #403F4C;
  --bg-secondary: #121420;
  --pink-color: #F0A6CA;
  --auxiliar-color: #B8BEDD;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #F5D8EF;
  --bg-clear: #F0A6CA;
  --bg-secondary: #F0E6EF;
  --pink-color: #9C89B8;
  --auxiliar-color: #B8BEDD;
}
 
.theme-icon {
  display: none;
}

.dark #darkIcon {
  display: block;
}

.light #lightIcon {
  display: block;
}

strong {
  color: var(--pink-color);
}

.Home {
  margin: 0;
  padding: 0;
}

body {
  color: black;
  background-color: white;
  overflow-y: scroll;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background-color: black;
}

body::-webkit-scrollbar-thumb {
  background-color: #FFCAD4;
}

.App {
  margin-left: 5rem;
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  min-height: 100vh;
}

.flexbox-container {
  display: flex;
  align-items: center;
  margin: auto;
  justify-content: center;
}

.flexbox-item {
  align-self: center;
}

.bubble {
  background-color: var(--bg-primary);
  padding: 15px 25px 50px 25px;
  border-left: 5px solid;
  border-color: var(--pink-color);
  margin-bottom: 20px;
}

.title {
  font-weight: 600;
    color: var(--pink-color);
    font-size: xx-large;
}
 body {
    color: black;
    background-color: white;
    margin: 0;
    padding: 0;
  }
  
  body::-webkit-scrollbar {
    width: 0.25rem;
  }
  
  body::-webkit-scrollbar-track {
    background: #1e1e24;
  }
  
  body::-webkit-scrollbar-thumb {
    background: var(--pink-color);
  }
  
  .App {
    margin-left: 5rem;
    padding: 1rem;
  }
  
  .navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 600ms ease;
  }
  
  .navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .nav-item {
    width: 100%;
  }
  
  .nav-item:last-child {
    margin-top: auto;
  }
  
  .nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    -webkit-filter: grayscale(30%) opacity(0.7);
            filter: grayscale(30%) opacity(0.7);
    transition: var(--transition-speed);
  }
  
  .nav-link:hover {
    -webkit-filter: grayscale(0%) opacity(1);
            filter: grayscale(0%) opacity(1);
    background: var(--bg-clear);
    color: var(--text-secondary);
  }
  
  .link-text {
    display: none;
    margin-left: 1rem;
  }
  
  .nav-link svg {
    font-size: 1.2rem;
    margin: 0 1.5rem;
  }
  
  .fa-primary {
    color: var(--pink-color);
  }
  
  .fa-primary,
  .fa-secondary {
    transition: var(--transition-speed);
  }
  
  .logo {
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 1rem;
    text-align: center;
    color: var(--text-secondary);
    background: var(--bg-clear);
    font-size: 1.5rem;
    letter-spacing: 0.3ch;
    width: 100%;
  }
  
  .logo svg {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: var(--transition-speed);
  }
  
  .logo-text
  {
    display: inline;
    position: absolute;
    left: -999px;
    transition: var(--transition-speed);
    margin-left: 2rem;
  }
  
  .navbar:hover .logo svg {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  
  /* Small screens */
  @media only screen and (max-width: 600px) {
    .navbar {
      bottom: 0;
      width: 100vw;
      height: 5rem;
    }
  
    .logo {
      display: none;
    }
  
    .navbar-nav {
      flex-direction: row;
    }
  
    .nav-link {
      justify-content: center;
    }
  
    .App {
      margin: 0;
    }
  }
  
  /* Large screens */
  @media only screen and (min-width: 600px) {
    .navbar {
      top: 0;
      width: 5rem;
      height: 100vh;
    }
  
    .navbar:hover {
      width: 16rem;
    }
  
    .navbar:hover .link-text {
      display: inline;
    }
  
    .navbar:hover .logo svg
    {
      margin-left: 11rem;
    }
  
    .navbar:hover .logo-text
    {
      left: 0px;
    }
  }
